export default {
	data() {
		return {
			page: 1,
			topicList: []
		};
	},
	methods: {
		getProject() {
			this.$api.getProject(this.page).then(res => {
				if (res.data.data.length > 0 && res.status === 200) {
					this.topicList.push(...res.data.data)
					this.page = this.page + 1
				} else {
					uni.showToast({
						icon: "none",
						title: "没有更多内容了哦",
						duration: 800
					})
				}
			})
		},
		gotoDetail(id) {
			uni.navigateTo({
				url: `/pages/projectDetail/projectDetail?id=${id}`
			})
		}
	},
	onLoad() {
		this.getProject()
	},
	onReachBottom() {
		this.getProject()
	}
}
